.main{
    width: 100%;
    height: 100vh;
    padding: 0;
}
.grid{
    display: grid;
    grid-template-columns: 50% 50%;
}
.slides{
    width: 100%;
    height:100vh;
    background:#08646E;
    
   
}
.img{
    width: 100%;
    height: 30rem;
    padding-top: 10rem;
}
.slides2{
    width: 100%;
    height:100vh;
    background:#097F8C;
    padding: 2rem;
    padding-top: 10rem;
    color: white;
   
}
.btnLight{
    display: inline-block;        
    border: solid #7d7d7d;
    border-width: 1px 0;
    border-radius: 1em;
    color: #757575;
    padding: 0.3em 1em;
    background: #fff;
    box-shadow: 0 1px 2px 0 #A0A0A0, 
                0 0 2px 0 #A0A0A0 inset, 
                0 -1.2em 0.45em -0.8em #FFFFFF inset, 
                0 -0.9em 1px #DADADA inset, 
                0 -0.9em 8px #EDEDED inset;
    font: 34px sans-serif;
    text-shadow: 0 1px 0 #FFFFFF;
    text-decoration: none;
    margin:20px;
}
.btnDark{
    display: inline-block;        
    border: solid goldenrod;
    border-width: 1px 0;
    border-radius: 1em;
    color: white;
    padding: 0.3em 1em;
    background: goldenrod;
    margin:20px;
    
    font: 34px sans-serif;
    text-shadow: 0 1px 0 #FFFFFF;
    text-decoration: none;
}