.main{
    width: 100%;
    height: 100vh;
    padding: 0;
}
.grid{
    display: grid;
    grid-template-columns: 50% 50%;
}
.slides{
    width: 100%;
    height:100vh;
    background:#08646E;
}
.igrid{
  display: grid;
  grid-template-columns: 45% 45%;
  grid-gap: 30px;
}
.img{
    width: 100%;
    height: 30rem;
    padding-top: 10rem;
}
.slides2{
    width: 100%;
    height:100vh;
    background:#097F8C;
    padding: 2rem;
    padding-top: 5rem;
    color: white;
   
}

.inputGroup {
    display: block;
    width: 100%;
    max-width: 100%;
    height: 82px;
    border: 0;
    background-color: #ffffff;
    border-bottom-left-radius: 41px;
    border-bottom-right-radius: 41px;
    border-top-left-radius: 41px;
    border-top-right-radius: 0;
    box-shadow: 0 17px 40px 0 rgba(75, 128, 182, 0.07);
    margin-bottom: 22px;
    position: relative;
    font-size: 17px;
    color: #a7b4c1;
    transition: opacity 0.2s ease-in-out, filter 0.2s ease-in-out,
      box-shadow 0.1s ease-in-out;
  }
  
  .inputGroup:hover {
    box-shadow: 0 14px 44px 0 rgba(0, 0, 0, 0.077);
  }
  
  .inputGroup input {
    position: absolute;
    border: 0;
    box-shadow: none;
    background-color: rgba(255, 255, 255, 0);
    top: 0;
    height: 65px;
    width: 100%;
    padding: 0 53px;
    box-sizing: border-box;
    z-index: 3;
    display: block;
    color: #1a6fc4;
    font-size: 17px;
    font-family: "Oxygen", sans-serif;
    transition: top 0.1s ease-in-out;
  }
  
  .inputGroup input::placeholder {
    color: rgba(0, 0, 0, 0);
  }
  
  .inputGroup input:focus,
  .inputGroup input:not(:placeholder-shown) {
    top: 17px;
    outline: none;
  }
  
  .inputGroup label {
    position: absolute;
    border: 0;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    padding: 0 53px;
    box-sizing: border-box;
    transition: all 0.1s ease-in-out;
    cursor: text;
  }
  
  .inputGroup input:focus + label,
  .inputGroup input:not(:placeholder-shown) + label {
    bottom: 20px;
    font-size: 13px;
    opacity: 0.7;
  }
  .btn{
    display: block;
    width: 100%;
    height: 4rem;
    margin-bottom: 1rem;
    padding: 1rem;
    overflow: hidden;
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    font-size: 20px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0px 1px 2px -1px rgba(0,0,0,0.02),
    0px 2px 4px -2px rgba(0,0,0,0.08),
    0px 4px 8px -4px rgba(0,0,0,0.10),
    0px 8px 16px -8px rgba(0,0,0,0.12);
    background: linear-gradient(180deg, rgba(9, 127, 140, 1), rgba(8, 100, 110, 1));
    border: 1px solid rgba(255, 255, 255, 0.3);
}
.btn:hover{
    text-decoration: none;
    color: white;
}